import city from '../../common/city.js'
import uniSelect from '../../components/lee-select/lee-select.vue'
export default {
	components:{
		uniSelect
	},
	//1.初始化模板变量
	data() {
		return {
			//获取自定义$commonConfig对象中的服务器地址
			serverUrl:this.$commonConfig.serverUrl,
			
			//获取定位城市处->上下图标切换
			downUpImg:"xiala-down.png",
			
			//获取定位城市
			cityName:'上海',//默认定位城市
			
			//城市选择面板
			isActive:false,
			listData: city,
			quickPanelData:[
				{
				title:'当前城市',
				navName: '当前',
				data:['上海'], //默认显示当前城市
				height: 150
			},
			{
				title:'热门城市',
				navName: '热',
				data:['上海','北京','成都','昆明','西安'],
				height: 224
			}
			]
			// listAttr: {
			// 	listBackgroundColor:'none',
			// 	titleFontSize: 28,
			// 	titleColor: '#333',
			// 	titleHeight: 60,
			// 	titleBackground: '#ccc',
			// 	titlePadding: 20,
			// 	itemHeight: 80,
			// 	itemFontSize: 28,
			// 	itemBorderBottom: '1px solid rgba(0, 0, 0, 0.1)',
			// 	itemColor: '#333'
			// },
			// navAttr: {
			// 	enable: true,
			// 	backgroundColor: 'rgba(0, 0, 0, 0.4)',
			// 	color: '#333',
			// 	activeColor: '#fff',
			// 	fontSize: 20,
			// 	itemPadding:'6 0',
			// 	borderRadius: 100,
			// 	padding:'20 0'
			// }
		}
	},
	
	//2.页面加载完成、页面卸载事件
	onLoad() {
		
	}, 
	
	//3.事件以及自定义方法存放处
	methods: {
		showCitySelect(){
			this.downUpImg="xiala-up.png";//切换上下图标
			this.isActive=true;
		},
		hideCitySelect(){
			this.downUpImg="xiala-down.png"; //切换上下图标
			this.isActive=false;
		},
		chooseItem(item) {
			this.downUpImg="xiala-down.png"; //切换上下图标
			this.isActive=false; //隐藏城市面板
			this.cityName=item; //赋值给当前定位城市
			this.quickPanelData[0].data=[item]; //赋值给面板当前城市
		}
	}, 

}
